// HOME
$black: #000;
$white: #fff;
$gray-text: #7C7C7C;
$gradient-bg: #E5E7F2;
$gray-bg: #F4F5FB;
$gray-hr: #B3B3B3;
$color-title: #233947;
$gray-form: #848B91;
$gray-cr: #484F55;
$gray-control: #B7B7B7;
$border-modal: #BCBCBC;
$blue-color: #54C9DE;
$green-color: #26B749;
$green-secondary: #10B17F;
$black-footer: #181B1E;
$header-bg: #2A2E33;
$red-color: #EF3E3E;
$main-font: 'Montserrat', sans-serif;

/// Mixin para inserção facilitada de breakpoints mobile
/// @param {string} $value - Resolução do breakpoint para inserção do @media.
/// @content Conteúdo de personalização do breakpoint.
/// @example @include breakpoint(tablet);
@mixin breakpoint($value: mobile) {
    @if map-has-key($breakpoints, $value) {
      @media screen and #{inspect(map-get($breakpoints, $value))} {
        @content;
      }
    }
    @else {
      @warn "Couldn't find a breakpoint named `#{$value}`.";
    }
  }
  
  // Valores para resolução de breakpoints
  $breakpoints: (
        'lowmobile' : (max-width: 640px),
          'mobile' : (max-width: 768px),
          'tablet' : (max-width: 920px),
          'notebook' : (max-width: 1366px),
          'fullhd' : (min-width: 1600px),
          'fullhd1920' : (min-width: 1920px)
  );

section.banner{
    background: url('https://i.ibb.co/gRMwFYG/bannerhome-2.png') no-repeat center;
    background-color: $gradient-bg;
    background-size: cover;
    padding: 145px 0;
    position: relative;
    @include breakpoint(){
        background: url('https://i.ibb.co/Qdd2Bfh/banner-mobile.pnghosting') no-repeat center;
        background-color: $gradient-bg !important;;
        background-size: cover !important;
        padding:  120px 0 70px;
    }

    @include breakpoint('fullhd'){
      padding: 180px 0;
    }

    @include breakpoint('fullhd1920'){
      padding: 240px 0;
    }
    
    h1{
      color: $white;
      font-size: 52px;
      font-style: italic;
      text-transform: uppercase;
      @include breakpoint(){
          font-size: 26px;
      }

      @include breakpoint('lowmobile'){
        font-size: 22px;
      }
    
      b{
          margin-left: 80px;
          font-weight: bold;
          @include breakpoint(){
            margin-left: 40px;
        }
      }
    }
  
    p{
        color: $white;
        font-size: 22px;
        font-weight: bold;

        @include breakpoint(){
            font-size: 18px;
            text-align: center;
            padding-top: 180px;
        }
    }

    .link-scroll{
      display: flex;
      justify-content: center;
      position: absolute;
      bottom: -50%;
      right: 0;
      left: 0;
      @include breakpoint(){
        bottom: -18%;
      }

      @include breakpoint('fullhd'){
        bottom: -65%;
      }

      @include breakpoint('fullhd1920'){
        bottom: -80%;
      }

      a{
        color: $white;
        transition: all .2s;

        &:hover{
          opacity: .5;
        }

        i{
          font-size: 2rem;
        }
      }
    }
  }
  
  section.sobre, section.sobre-sz, section.sobre-invs{
    padding: 70px 0;
    background: linear-gradient(180deg, $gradient-bg 0%, $white 100%);
    @include breakpoint(){
        padding: 60px 0 30px;
    }
  
    .img-width{
        width: 110%;
        @include breakpoint(){
            width: 100%;
        }
    }
    
    .title-d{
      padding: 70px 170px 0;
      @include breakpoint(){
          padding: 40px 0 0;
      }
    }
    
    .hr-d{
      margin-bottom: 100px;
    }
    
    .box-df{
      box-shadow: 1px 2px 15px 0 rgba(0,0,0,0.2);
      border-radius: 10px;
      background: $white;
      height: 300px;
      padding: 30px 15px;
      @include breakpoint(){
          margin-bottom: 30px;
      }
      
      .div-img{
        display: flex;
        justify-content: center;
        margin-bottom: 20px;
      }
  
      .mg-top{
          margin-top: 28px;
      }
      
      .hr-g{
        border-top: 3px solid $gray-hr;
        width: 70px;
        margin-left: -15px;
      }
      
      p{
        strong{
          color: $black;
        }
      }
    }
  }
  
  section.solucoes, section.solucoes-sz, section.solucoes-invs{
      overflow: hidden;
      background: $gray-bg;
      padding: 80px 0 90px;
      @include breakpoint(){
          padding: 60px 0 100px;
      }
  
      a{
          display: block;
  
          &:hover{
              transition: all .4s;
              text-decoration: none;
              transform: scale(1.02, 1.02);
          }
      }
  
      .bg-sprint{
          background: url('https://i.ibb.co/LhFNdB3/bg-img-sprint-zero.png') no-repeat;
          box-shadow: 1px 2px 15px 0 rgba(0, 0, 0, 0.6);
          background-size: cover;
          padding: 50px 40px 70px;
          margin-bottom: 40px;
          border-radius: 10px;
          @include breakpoint(){
            padding: 40px 40px 70px;
          }
      }
  
      .bg-innovation{
          background: url('https://i.ibb.co/dWV3JQc/bg-img-innovation-squad.png') no-repeat;
          box-shadow: 1px 2px 15px 0 rgba(0, 0, 0, 0.6);
          background-size: cover;
          padding: 50px 40px 70px;
          border-radius: 10px;
          @include breakpoint(){
            padding: 40px 40px 70px;
          }
      }
  
      h3{
        color: $white;
        line-height: 2.5rem;
        @include breakpoint(){
            line-height: 1.8rem;
        }
      }
  
      .link-area{
          margin-top: 60px;
          @include breakpoint(){
            margin-top: 50px;
          }

          .a-style{
            transition: all .4s;
            text-transform: uppercase;
            background: $green-secondary;
            font-family: $main-font;
            font-weight: bold;
            font-size: 15px;
            color: $white;
            border-radius: 25px;
            padding: 16px 80px;
            box-shadow: 0 2px 14px 0 rgba(0,0,0,0.5);
            @include breakpoint('lowmobile'){
              padding: 16px 40px;
            }
            
            &:hover{
              text-decoration: none;
            }
          }
      }

      .img-solucoes{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        @include breakpoint(){
          margin-bottom: 20px;
        }
      }
      
      .slider-concore{
        padding-top: 80px;

        h2{
          padding: 0 140px;
          @include breakpoint(){
            padding: 0;
          }
        }

        h3{
          color: $color-title;
          line-height: 2rem;
          @include breakpoint(){
            line-height: 1.4rem;
          }
        }

        h4{
          padding-top: 60px;
          @include breakpoint(){
            padding-top: 20px;
          }
        }

        .carousel-item{
          img{
            width: 110%;
          }
        }

        .car-control{
          i{
            color: $gray-control;
            font-size: 3rem;
          }
        }

        .carousel-control-prev {
          left: -10%;
          top: 40%;
          @include breakpoint(){
            left: 30%;
            top: 100%;
          }
        }

        .carousel-control-next {
          right: -10%;
          top: 40%;
          @include breakpoint(){
            right: 30%;
            top: 100%;
          }
        }

        .carousel-control-next, .carousel-control-prev {
          opacity: 1;
        }
      }
  }

// HEADER FOOTER
