$green-color: #26B749;
$green-secondary: #10B17F;
$black-footer: #181B1E;
$black-active: #1F242A;
$header-bg: #2A2E33;
$red-color: #EF3E3E;
$main-font: 'Montserrat', sans-serif;

/// Mixin para inserção facilitada de breakpoints mobile
/// @param {string} $value - Resolução do breakpoint para inserção do @media.
/// @content Conteúdo de personalização do breakpoint.
/// @example @include breakpoint(tablet);
@mixin breakpoint($value: mobile) {
  @if map-has-key($breakpoints, $value) {
    @media screen and #{inspect(map-get($breakpoints, $value))} {
      @content;
    }
  }
  @else {
    @warn "Couldn't find a breakpoint named `#{$value}`.";
  }
}

// Valores para resolução de breakpoints
$breakpoints: (
        'lowmobile' : (max-width: 640px),
        'mobile' : (max-width: 768px),
        'tablet' : (max-width: 920px),
        'notebook' : (max-width: 1366px),
        'fullhd' : (min-width: 1600px),
        'fullhd1920' : (min-width: 1920px)
);

footer{
    padding: 100px 0 0;
    background: $black-footer;
    @include breakpoint(){
      padding: 50px 0 0;
    }
    
    ul{
      display: flex;
      justify-content: center;
      list-style: none;
      margin-top: 40px;
      
      li{
        margin-right: 30px;
      }
      
      a{
        img{
          transition: all .4s;
        }

        &:hover{
          img{
            transform: scale(1.2, 1.2);
          }
        }
      }
    }
    
    h5{
      padding-bottom: 20px;
      font-family: $main-font;
      font-weight: bold;
      font-size: 16px;
      color: $white;
      @include breakpoint(){
        padding: 30px 0 0;
      }
    }
    
    p{
      color: $white;
      @include breakpoint(){
        margin-bottom: 5px;
      }
  
      a{
          color: $white;
          transition: all .4s;
  
          &:hover{
              text-decoration: none;
              color: $green-color;
          }
      }
    }
    
    .form-style{
      input{
        background: transparent;
        border: 1px solid $gray-form;
        border-top: none;
        border-left: none;
        border-right: none;
        border-radius: 0;
        
        &:focus{
          background: $gray-form;
          box-shadow: none;
        }
      }
      
      .button-area{
        text-align: right;
        padding: 15px 0 40px;
        @include breakpoint(){
          padding: 15px 0 10px;
        }
        
        button{
          cursor: pointer;
          transition: all .4s;
          font-family: $main-font;
          font-weight: bold;
          font-size: 14px;
          background: transparent;
          border: 2px solid $green-secondary;
          border-radius: 20px;
          color: $green-secondary;
          padding: 8px 40px;
          @include breakpoint(){
            width: 100%;
          }

          &:hover{
            background: $green-secondary;
            color: $white;  
          }
        }
      }
    }
    
    hr{
      border-top: 1px solid $gray-cr;
    }
    
    .creditos{
      font-size: 13px;
      color: $gray-cr;
    }
  }
  
  header{
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.3);
    background: $header-bg;
    padding: 20px 0;
    @include breakpoint(){
      position: fixed;
      width: 100%;
      z-index: 999;
      .navbar-light .navbar-toggler{
        border: none !important;
        outline: none;
      }
    }
  
    nav{
      @include breakpoint(){
        padding: 0 !important;

        .menu-holder{
          padding-left: 15px !important;
        }

        button{
          padding-right: 15px !important;
        }
      }
      .mx-auto{
        margin-right: 0 !important;
      }

      ul{
        @include breakpoint(){
          text-align: right;
          margin-top: 20px;
        }
        li{
          margin-right: 30px;
          @include breakpoint(){
            margin-right: 0;
            text-align: center;
          }

          a{
            transition: all .4s;
            text-transform: uppercase;
            font-family: $main-font;
            font-weight: bold;
            font-size: 13px;
            color: $gray-text;
            @include breakpoint(){
              display: block;
              line-height: 3.2rem;

              &:active{
                background: $black-active;
                color: $white;
              }
            }
            
            &:hover{
              text-decoration: none;
              color: $white;
            }

            &:active{
              color: $white;
            }
          }
          
          &:last-child{
            margin-right: 0;
          }
        }
      }
    }
  }

  .active-link {
    text-decoration: none;
    color: #fff;

    @media screen and (max-width: 768px) {
      background: #1F242A;
    }
  }

