.hidden-elem{
  display: none;
}

.show-elem{
  display: block;
}

h1, h2{
  font-family: $main-font;
  font-weight: bold;
  font-size: 32px;

  @include breakpoint(){
      font-size: 26px;
  }

  @include breakpoint('lowmobile'){
    font-size: 22px;
  }
}

h2{
  color: $color-title;
}

h3{
    font-family: $main-font;
    font-weight: bold;
    font-size: 26px;
    @include breakpoint(){
        font-size: 18px;
    }
  }

  h4{
    font-family: $main-font;
    font-weight: bold;
    font-size: 18px;
    color: $gray-text;
  }

p{
  font-family: $main-font;
  font-weight: 400;
  font-size: 16px;
  color: $gray-text;

  @include breakpoint('lowmobile'){
