  }
}

img{
  max-width: 100%;
}

.divider1{
    margin-bottom: 30px;
}

.divider2{
    margin-bottom: 50px;
}

.text-center-mobile{
  @include breakpoint(){
    text-align: center;
  }
}


.modal {
  background-color: rgba(255,255,255,0.7);
}
.modal-open .modal {
  display: flex !important;
  align-items: center;
  justify-content: center;
  padding-right: 0 !important; 
}
#reserva-sz, #contato-invs{
  
  .modal-header{
    border: none;
    position: relative;

    span i{
      color: $red-color;
    }
  }

  .modal-content{
    border: none;
    border-radius: 10px; 
    box-shadow: 0 2px 14px 0 rgba(0,0,0,0.3);
  }

  .modal-body{
    padding-top: 0;
  }

  h5{
    font-family: $main-font;
    font-weight: bold;
    font-size: 24px;
    color: $color-title;
    text-align: center;
    line-height: 1.8rem;
    padding: 30px 20px 0;
    @include breakpoint(){
      font-size: 22px;
    }
  }

  .close{
    position: absolute;
    right: 15px;
    opacity: 1;
    @include breakpoint(){
      padding: 1rem 1rem 0 0;
    }

    &:hover{
      opacity: .7;
    }
  }

  .form-style{
    padding: 0 60px;
    @include breakpoint(){
      padding: 0 20px;
    }

    input{
      background: transparent;
      border: 1px solid $border-modal;
      border-top: none;
      border-left: none;
      border-right: none;
      border-radius: 0;
      
      &:focus{
        background: $border-modal;
        box-shadow: none;
      }
    }
    
    .button-area{
      text-align: right;
      padding: 30px 0 40px;
      @include breakpoint(){
        padding: 20px 0 30px;
      }
      
      button{
        text-transform: uppercase;
        cursor: pointer;
        transition: all .4s;
        font-family: $main-font;
        font-weight: bold;
        font-size: 16px;
        background: $green-secondary;
        border-radius: 25px;
        border: none;
        color: $white;
        padding: 12px 40px;
        width: 220px;
        box-shadow: 0 2px 14px 0 rgba(0,0,0,0.3);
        @include breakpoint(){
          width: 100%;
        }
      }
    }
  }

  .success-msgn{
    padding: 10px 0 20px;

    img{
      margin-bottom: 30px;
    }

    h5{
      padding: 0 70px;
    }
  }


}